

const Letter = (props) => {

  let theClassName = "char";
  theClassName += + props.vowel ? " vowel" : "";

  return ( 
    <div className="Letter">
      <div className={theClassName}>{props.letter}</div>
      <div className="name">
        <span>{props.names && props.names.ge ? props.names.ge : "-"}</span>
        /
        <span>{props.names && props.names.en ? props.names.en : props.name ? props.name: "-"}</span>
      </div>
    </div>
   );
}
 
export default Letter;