
import Letter from "./Letter"
import { Link } from "react-router-dom";

const LettersList = (props) => {
  return (
    <main>
      <div className="LettersList">
        {props.alphabet.map((e, i) => (
          <Link key={i} to={`/letter/${i}`}>
            <Letter {...e} />
          </Link>
        ))}
      </div>
    </main>
  );
}
 
export default LettersList;