import { Link } from "react-router-dom";

const Header = () => {
  const links = [
    {
      name: "Main",
      href: "/"
    },
    {
      name: "Settings",
      href: "/settings"
    },
    {
      name: "Help",
      href: "/help"
    },
    {
      name: "About",
      href: "/about"
    },
  ];
  return (
    <>
      <header>
        <p>
          This is a header!
        </p>
        <nav>
          <ul>
            {links.map((e, i) => <li key={i}><Link to={e.href}>{e.name}</Link></li>)}
          </ul>
        </nav>
      </header>
    </>
  );
}
 
export default Header;