import { Link, useParams } from "react-router-dom";

const LetterDetail = (props) => {
  const { id } = useParams();
  const idNumber = parseInt(id);
  const {letter, link, names, order, picture, transcription, sound, vowel} = {...props.alphabet[id]};
  
  const theClassName = vowel ? "detail__char vowel" : "detail__char";
  const prevLink = `/letter/${idNumber > 0 ? idNumber - 1 : 32}`;
  const nextLink = `/letter/${idNumber < 32 ? idNumber + 1 : 0}`;

  console.log(link, picture, sound, vowel);

  return (
    <>
      <div className="detail">
        <div className="detail__letter">
          <div className="detail__order"><p>{order}</p></div>
          <div className="detail__transcription"><p>{transcription}</p></div>
          <div className="detail__close"><p><Link to="/">X</Link></p></div>
          <div className="detail__prev"><Link to={prevLink}><p>{"<"}</p></Link></div>
          <div className={theClassName}><div>{letter}</div></div>
          <div className="detail__next"><Link to={nextLink}><p>{">"}</p></Link></div>
          <div className="detail__name">
            <p>
              <span>{names.ge ? names.ge : "-"}</span>
              /
              <span>{names.en ? names.en : "-"}</span>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
 
export default LetterDetail;